@import url('https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap');

@font-face {
  font-family: 'Alloy Ink';
  src: url('./AlloyInk-nRLyO.ttf') format('truetype'),
       url('./AlloyInk-lgdWw.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Quill Ink';
  src: url('./Ink.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@keyframes backgroundShiftLight {
  0% { background-color: #FFA500; } /* Orange */
  50% { background-color: #FFC0CB; } /* Pink */
  100% { background-color: #F08080; } /* Coral */
}

@keyframes backgroundShiftDark {
  0% { background-color: #8B4500; } /* Dark Orange */
  50% { background-color: #8B3A62; } /* Dark Pink */
  100% { background-color: #8B0000; } /* Dark Red */
}

@keyframes dotMoveLeft {
  from { background-position: 0 0; }
  to { background-position: 30px 0; }
}

@keyframes dotMoveRight {
  from { background-position: 0 0; }
  to { background-position: -30px 0; }
}

body {
  font-family: 'Quill Ink', 'Patrick Hand', 'Comic Sans MS', cursive, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

body.light {
  animation: backgroundShiftLight 120s infinite;
  background-image: 
    radial-gradient(circle, #ffffff 10%, transparent 10%),
    radial-gradient(circle, #ffffff 10%, transparent 10%);
}

body.dark {
  animation: backgroundShiftDark 120s infinite;
  background-image: 
    radial-gradient(circle, #000000 10%, transparent 10%),
    radial-gradient(circle, #000000 10%, transparent 10%);
}

body::before,
body::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: inherit;
  background-size: inherit;
  animation: dotMoveLeft 10s linear infinite;
}

body::after {
  top: 15px;
  animation-name: dotMoveRight;
}

/* App Container */
.App {
  text-align: center;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  position: relative;
  z-index: 1;
}

.App.light {
  background: rgba(255, 255, 255, 0.7);
  color: #000000;
}

.App.dark {
  background: rgba(0, 0, 0, 0.7);
  color: #ffffff;
}

.App div {
  color: inherit;
}

/* Headers */
h2, h3, h4 {
  font-family: 'Alloy Ink';
  margin: 20px 0;
}

.App.light h2, .App.light h3, .App.light h4 {
  color: #000000;
  text-shadow: 
    -1px -1px 0 #fff,  
     1px -1px 0 #fff,
    -1px  1px 0 #fff,
     1px  1px 0 #fff;
}

.App.dark h2, .App.dark h3, .App.dark h4 {
  color: #ffffff;
  text-shadow: 
    -1px -1px 0 #000,  
     1px -1px 0 #000,
    -1px  1px 0 #000,
     1px  1px 0 #000;
}

/* Inputs */
input[type="text"], input[type="number"] {
  padding: 10px;
  margin: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 200px;
  font-size: 16px;
  font-family: 'Patrick Hand', 'Comic Sans MS', cursive, sans-serif;
}

/* Buttons */
button {
  padding: 10px 20px;
  margin: 10px;
  border: none;
  border-radius: 10px;
  background-color: #e58a00;
  color: white;
  font-size: 16px;
  font-family: 'Patrick Hand', 'Comic Sans MS', cursive, sans-serif;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-shadow: 
    -1px -1px 0 #000,  
     1px -1px 0 #000,
    -1px  1px 0 #000,
     1px  1px 0 #000;
}

button:hover {
  background-color: #8e5808;
}

/* Lists */
ul {
  list-style: none;
  padding: 0;
}

li {
  margin: 10px 0;
}

/* Other elements */
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.centered-image-container {
  display: flex;
  justify-content: center;
}

.centered-image {
  max-width: 20vw;
  height: auto;
}

.smalltext {
  font-size: small;
}

.App.light .smalltext {
  color: #000000;
}

.App.dark .smalltext {
  color: #ffffff;
}

/* Theme toggle button */
.theme-toggle {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  cursor: pointer;
  background-color: var(--toggle-bg-color);
  color: var(--toggle-color);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.App.light {
  --toggle-bg-color: white;
  --toggle-color: black;
}

.App.dark {
  --toggle-bg-color: black;
  --toggle-color: white;
}